.btn {
 border: none;
 background-color: #4D7EA8;
 height:50px;
 width:250px;
 cursor:pointer;
 color: white;
 border-radius: 2px;
 -webkit-transition: background-color .3s ease-in-out;
 -o-transition: background-color .3s ease-in-out;
 transition: background-color .3s ease-in-out;
}

.btn:hover {
 background-color: #233A4D;
 color: white;
 -webkit-transition: background-color .3s ease-in-out;
 -o-transition: background-color .3s ease-in-out;
 transition: background-color .3s ease-in-out;
}


